import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ButtonModule } from 'primeng/button';

@Component({
  selector: 'pla-button-outlined',
  imports: [ButtonModule],
  templateUrl: './pla-button-outlined.component.html',
  styleUrl: './pla-button-outlined.component.css'
})
export class PlaButtonOutlinedComponent {
  @Input() label: string = '';
  @Input() icon: string = '';
  @Output() onClick = new EventEmitter<void>();

  onActionClick() {
    this.onClick.emit();
  }
}
